import { _decorator, Component, Node, RigidBody, v3, input, Input, EventMouse } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('BirdCtrl')
export class BirdCtrl extends Component {

    @property(Node)
    Bird:Node = null!

    start() {
        input.on(Input.EventType.MOUSE_DOWN,this.onMouseClick,this);
    }

    onMouseClick(event: EventMouse){
        this.Bird.getComponent(RigidBody).setLinearVelocity(v3(0,300,0));
    }
    /*
    update(deltaTime: number) {
        
    }//还用不上
    */

}

